'use client';

import { useRouter } from 'next/navigation';
import { createClient } from '@/lib/supabase/client';
import { DollarSign, ShoppingCart, Clock, Package, ChevronRight, Bell, AlertTriangle, CheckCircle, TrendingUp, Users, UtensilsCrossed, Building2, Archive, BarChart3, Zap } from 'lucide-react';
import { useState, useEffect } from 'react';
import { useLanguage } from '@/lib/i18n/context';

interface DashboardClientProps {
  restaurants: any[];
}

interface TopItem {
  name: string;
  revenue: number;
  count: number;
}

interface StaffPerformance {
  name: string;
  orders: number;
  revenue: number;
  rating: number;
}

export default function DashboardClient({ restaurants }: DashboardClientProps) {
  const router = useRouter();
  const supabase = createClient();
  const { language } = useLanguage();
  const [loading, setLoading] = useState(false);
  const [activeRestaurant, setActiveRestaurant] = useState<any>(null);
  const [period, setPeriod] = useState<'daily' | 'weekly' | 'monthly' | 'yearly'>('daily');
  const [stats, setStats] = useState({
    todayRevenue: 0,
    todayOrders: 0,
    weekRevenue: 0,
    monthRevenue: 0,
    yearRevenue: 0,
    activeOrders: 0,
    lowStockItems: 0,
    outOfStockItems: 0,
    avgOrderValue: 0,
    totalCustomers: 0,
    growthPercent: 0
  });
  const [topMenuItems, setTopMenuItems] = useState<TopItem[]>([]);
  const [topTables, setTopTables] = useState<any[]>([]);
  const [topStaff, setTopStaff] = useState<StaffPerformance[]>([]);
  const [insights, setInsights] = useState<any[]>([]);
  const [recentOrders, setRecentOrders] = useState<any[]>([]);
  const [loadingData, setLoadingData] = useState(true);
  const [todos, setTodos] = useState<any[]>([]);
  const [alerts, setAlerts] = useState<any[]>([]);

  useEffect(() => {
    if (restaurants.length > 0) {
      // Check localStorage for selected restaurant
      const savedRestaurantId = localStorage.getItem('selected_restaurant_id');
      if (savedRestaurantId) {
        const saved = restaurants.find(r => r.id === savedRestaurantId);
        if (saved) {
          setActiveRestaurant(saved);
        } else {
          setActiveRestaurant(restaurants[0]);
          localStorage.setItem('selected_restaurant_id', restaurants[0].id);
        }
      } else {
        setActiveRestaurant(restaurants[0]);
        localStorage.setItem('selected_restaurant_id', restaurants[0].id);
      }
    }

    // Listen for restaurant changes from other pages
    const handleRestaurantChange = () => {
      const savedRestaurantId = localStorage.getItem('selected_restaurant_id');
      if (savedRestaurantId && restaurants.length > 0) {
        const saved = restaurants.find(r => r.id === savedRestaurantId);
        if (saved && saved.id !== activeRestaurant?.id) {
          setActiveRestaurant(saved);
        }
      }
    };

    window.addEventListener('restaurantChanged', handleRestaurantChange);
    window.addEventListener('storage', handleRestaurantChange);

    return () => {
      window.removeEventListener('restaurantChanged', handleRestaurantChange);
      window.removeEventListener('storage', handleRestaurantChange);
    };
  }, [restaurants]);

  useEffect(() => {
    if (activeRestaurant) {
      loadDashboardData();
    }
  }, [activeRestaurant]);

  async function loadDashboardData() {
    if (!activeRestaurant) return;
    
    setLoadingData(true);
    try {
      const now = new Date();
      const today = new Date(now);
      today.setHours(0, 0, 0, 0);
      const tomorrow = new Date(today);
      tomorrow.setDate(tomorrow.getDate() + 1);

      // Period calculations
      const weekStart = new Date(today);
      weekStart.setDate(today.getDate() - 7);
      
      const previousWeekStart = new Date(weekStart);
      previousWeekStart.setDate(previousWeekStart.getDate() - 7);

      const monthStart = new Date(today.getFullYear(), today.getMonth(), 1);
      const yearStart = new Date(today.getFullYear(), 0, 1);

      // Get all paid orders for calculations
      const { data: allOrders } = await supabase
        .from('orders')
        .select('*, order_items(*)')
        .eq('restaurant_id', activeRestaurant.id)
        .eq('payment_status', 'paid')
        .gte('paid_at', yearStart.toISOString())
        .order('paid_at', { ascending: false });
      
      // Get active orders
      const { data: activeOrdersData } = await supabase
        .from('orders')
        .select('*')
        .eq('restaurant_id', activeRestaurant.id)
        .not('status', 'in', '(completed,cancelled)');

      // Get menu items for top performers
      const { data: menuItems } = await supabase
        .from('menu_items')
        .select('*')
        .eq('restaurant_id', activeRestaurant.id);

      // Get staff for performance ranking
      const { data: staff } = await supabase
        .from('staff')
        .select('*')
        .eq('restaurant_id', activeRestaurant.id);

      // Get tables
      const { data: tables } = await supabase
        .from('tables')
        .select('*')
        .eq('restaurant_id', activeRestaurant.id);

      // Get stock items
      const { data: stockItems } = await supabase
        .from('stock_items')
        .select('*, ingredient:ingredients(*)')
        .eq('restaurant_id', activeRestaurant.id);

      if (allOrders) {
        // Calculate revenue for different periods
        const todayOrders = allOrders.filter(o => {
          const paidAt = new Date(o.paid_at);
          return paidAt >= today && paidAt < tomorrow && o.status !== 'cancelled';
        });

        const weekOrders = allOrders.filter(o => {
          const paidAt = new Date(o.paid_at);
          return paidAt >= weekStart && o.status !== 'cancelled';
        });

        const previousWeekOrders = allOrders.filter(o => {
          const paidAt = new Date(o.paid_at);
          return paidAt >= previousWeekStart && paidAt < weekStart && o.status !== 'cancelled';
        });

        const monthOrders = allOrders.filter(o => {
          const paidAt = new Date(o.paid_at);
          return paidAt >= monthStart && o.status !== 'cancelled';
        });

        const yearOrders = allOrders.filter(o => {
          const paidAt = new Date(o.paid_at);
          return paidAt >= yearStart && o.status !== 'cancelled';
        });

        const todayRevenue = todayOrders.reduce((sum, o) => sum + (o.total || 0), 0);
        const weekRevenue = weekOrders.reduce((sum, o) => sum + (o.total || 0), 0);
        const previousWeekRevenue = previousWeekOrders.reduce((sum, o) => sum + (o.total || 0), 0);
        const monthRevenue = monthOrders.reduce((sum, o) => sum + (o.total || 0), 0);
        const yearRevenue = yearOrders.reduce((sum, o) => sum + (o.total || 0), 0);

        // Calculate growth
        const weekGrowth = previousWeekRevenue > 0 ? ((weekRevenue - previousWeekRevenue) / previousWeekRevenue) * 100 : 0;

        // Calculate average order value
        const avgOrderValue = weekOrders.length > 0 ? weekRevenue / weekOrders.length : 0;

        // Count unique customers (from order_items or orders)
        const uniqueCustomers = new Set(weekOrders.map(o => o.id)).size;

        // Calculate top menu items
        const itemRevenue: { [key: string]: TopItem } = {};
        weekOrders.forEach(order => {
          order.order_items?.forEach((item: any) => {
            const itemName = item.item_name || item.name;
            if (!itemRevenue[itemName]) {
              itemRevenue[itemName] = { name: itemName, revenue: 0, count: 0 };
            }
            itemRevenue[itemName].revenue += item.subtotal || 0;
            itemRevenue[itemName].count += item.quantity || 1;
          });
        });

        const topItems = Object.values(itemRevenue)
          .sort((a, b) => b.revenue - a.revenue)
          .slice(0, 5);

        // Calculate top tables
        const tableRevenue: { [key: number]: any } = {};
        weekOrders.forEach(order => {
          if (order.table_number) {
            if (!tableRevenue[order.table_number]) {
              tableRevenue[order.table_number] = { tableNumber: order.table_number, orders: 0, revenue: 0, avgOrder: 0 };
            }
            tableRevenue[order.table_number].orders += 1;
            tableRevenue[order.table_number].revenue += order.total || 0;
          }
        });

        const topTablesList = Object.values(tableRevenue)
          .map((t: any) => ({
            ...t,
            avgOrder: t.revenue / t.orders
          }))
          .sort((a, b) => b.revenue - a.revenue)
          .slice(0, 5);

        // Calculate top staff
        const staffPerformance: { [key: string]: StaffPerformance } = {};
        weekOrders.forEach(order => {
          if (order.staff_id && staff) {
            const staffMember = staff.find(s => s.id === order.staff_id);
            const staffName = staffMember?.name || 'Unknown';
            if (!staffPerformance[order.staff_id]) {
              staffPerformance[order.staff_id] = { name: staffName, orders: 0, revenue: 0, rating: 0 };
            }
            staffPerformance[order.staff_id].orders += 1;
            staffPerformance[order.staff_id].revenue += order.total || 0;
          }
        });

        const topStaffList = Object.values(staffPerformance)
          .sort((a, b) => b.revenue - a.revenue)
          .slice(0, 5);

        // Calculate stock metrics
        let lowStockCount = 0;
        let outOfStockCount = 0;

        if (stockItems) {
          stockItems.forEach(item => {
            if (item.quantity_on_hand === 0) {
              outOfStockCount++;
            } else if (item.ingredient && item.quantity_on_hand <= item.ingredient.reorder_level) {
              lowStockCount++;
            }
          });
        }

        // Generate insights based on data
        const newInsights: any[] = [];

        if (weekGrowth > 0) {
          newInsights.push({
            id: 'growth',
            type: 'positive',
            icon: TrendingUp,
            title: language === 'en' ? 'Strong Growth' : 'Pertumbuhan Kuat',
            message: `${weekGrowth.toFixed(1)}% pertumbuhan pendapatan minggu ini`,
            color: 'green'
          });
        }

        if (topItems.length > 0) {
          newInsights.push({
            id: 'top-item',
            type: 'info',
            icon: UtensilsCrossed,
            title: language === 'en' ? 'Top Seller' : 'Penjual Terbaik',
            message: `${topItems[0].name} adalah menu terlaris dengan ${topItems[0].count} penjualan`,
            color: 'blue'
          });
        }

        if (topTablesList.length > 0) {
          newInsights.push({
            id: 'top-table',
            type: 'info',
            icon: Building2,
            title: language === 'en' ? 'Popular Table' : 'Meja Populer',
            message: `Meja ${topTablesList[0].tableNumber} adalah yang paling ramai minggu ini`,
            color: 'purple'
          });
        }

        if (topStaffList.length > 0) {
          newInsights.push({
            id: 'top-staff',
            type: 'info',
            icon: Users,
            title: language === 'en' ? 'Top Staff' : 'Staf Terbaik',
            message: `${topStaffList[0].name} mencatat ${formatCurrency(topStaffList[0].revenue)} penjualan`,
            color: 'indigo'
          });
        }

        setStats({
          todayRevenue,
          todayOrders: todayOrders.length,
          weekRevenue,
          monthRevenue,
          yearRevenue,
          activeOrders: activeOrdersData?.length || 0,
          lowStockItems: lowStockCount,
          outOfStockItems: outOfStockCount,
          avgOrderValue: Math.round(avgOrderValue),
          totalCustomers: uniqueCustomers,
          growthPercent: Math.round(weekGrowth * 10) / 10
        });

        setTopMenuItems(topItems);
        setTopTables(topTablesList);
        setTopStaff(topStaffList);
        setInsights(newInsights);
        setRecentOrders(todayOrders.slice(0, 5));

        // Generate alerts
        const newAlerts: any[] = [];

        if (outOfStockCount > 0) {
          newAlerts.push({
            id: 'stock-out',
            type: 'error',
            message: `${outOfStockCount} item ${language === 'en' ? 'out of stock' : 'habis stok'}`,
            action: language === 'en' ? 'Check Inventory' : 'Cek Stok',
            link: '/dashboard/inventory'
          });
        }
        if (lowStockCount > 0) {
          newAlerts.push({
            id: 'stock-low',
            type: 'warning',
            message: `${lowStockCount} item ${language === 'en' ? 'low stock' : 'stok menipis'}`,
            action: language === 'en' ? 'Check Inventory' : 'Cek Stok',
            link: '/dashboard/inventory'
          });
        }

        if (activeOrdersData && activeOrdersData.length > 5) {
          newAlerts.push({
            id: 'orders-pending',
            type: 'info',
            message: `${activeOrdersData.length} ${language === 'en' ? 'orders in progress' : 'pesanan sedang diproses'}`,
            action: language === 'en' ? 'View Orders' : 'Lihat Pesanan',
            link: '/dashboard/orders'
          });
        }

        setAlerts(newAlerts);

        // Generate todos
        const newTodos: any[] = [];

        if (stockItems) {
          stockItems.forEach(item => {
            if (item.quantity_on_hand === 0 && item.ingredient) {
              newTodos.push({
                id: `restock-${item.id}`,
                text: `Restock ${item.ingredient.name}`,
                priority: 'high',
                link: '/dashboard/inventory'
              });
            } else if (item.ingredient && item.quantity_on_hand <= item.ingredient.reorder_level) {
              newTodos.push({
                id: `order-${item.id}`,
                text: `Pesan ${item.ingredient.name} (stok: ${item.quantity_on_hand} ${item.ingredient.unit})`,
                priority: 'medium',
                link: '/dashboard/inventory'
              });
            }
          });
        }

        setTodos(newTodos.slice(0, 5));
      }
    } catch (error) {
      console.error('Error loading dashboard data:', error);
    } finally {
      setLoadingData(false);
    }
  }

  const handleSignOut = async () => {
    setLoading(true);
    await supabase.auth.signOut();
    router.push('/');
    router.refresh();
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };

  const formatTime = (dateString: string) => {
    return new Date(dateString).toLocaleTimeString('id-ID', {
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getStatusColor = (status: string) => {
    const colors = {
      'pending': 'bg-yellow-100 text-yellow-800',
      'preparing': 'bg-blue-100 text-blue-800',
      'ready': 'bg-green-100 text-green-800',
      'completed': 'bg-gray-100 text-gray-800',
      'cancelled': 'bg-red-100 text-red-800'
    };
    return colors[status as keyof typeof colors] || 'bg-gray-100 text-gray-800';
  };

  if (restaurants.length === 0) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-[#FF5A5F]/5 via-white to-[#8B5CF6]/5 flex items-center justify-center">
        <div className="text-center max-w-md p-8">
          <Package className="w-20 h-20 text-gray-300 mx-auto mb-6" />
          <h2 className="text-3xl font-bold text-gray-900 mb-4">Belum Ada Bisnis</h2>
          <p className="text-gray-600 mb-8">
            Tambahkan bisnis pertama Anda untuk mulai menggunakan KadaiPOS
          </p>
          <button 
            className="px-8 py-4 bg-gradient-to-r from-[#FF5A5F] to-[#8B5CF6] text-white rounded-xl font-semibold hover:shadow-lg hover:scale-105 transition-all"
            onClick={() => router.push('/dashboard/setup')}
          >
            Tambah Bisnis
          </button>
        </div>
      </div>
    );
  }

  return (
    <>
      {/* Top Header */}
      <header className="bg-white border-b border-gray-200 shadow-sm sticky top-0 z-40">
        <div className="px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-xl font-bold text-gray-900">
                {activeRestaurant?.name || 'Dashboard'}
              </h2>
              <p className="text-sm text-gray-600">
                {new Date().toLocaleDateString('id-ID', { 
                  weekday: 'long', 
                  day: 'numeric',
                  month: 'long',
                  year: 'numeric'
                })}
              </p>
            </div>
          </div>
        </div>
      </header>

        {/* Main Dashboard Content */}
        <main className="px-4 sm:px-6 lg:px-8 py-8">
          {loadingData ? (
            <div className="flex items-center justify-center py-20">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[var(--color-accent)]"></div>
            </div>
          ) : (
          <>
            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all">
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-[var(--color-accent)]/10 rounded-xl flex items-center justify-center">
                    <DollarSign className="w-6 h-6 text-[var(--color-accent)]" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  {formatCurrency(stats.monthRevenue)}
                </h3>
                <p className="text-sm text-gray-600">
                  Pendapatan Bulan Ini
                </p>
              </div>

              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all">
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-[#8B5CF6]/10 rounded-xl flex items-center justify-center">
                    <ShoppingCart className="w-6 h-6 text-[#8B5CF6]" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  {stats.todayOrders}
                </h3>
                <p className="text-sm text-gray-600">Pesanan Selesai</p>
              </div>

              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all relative">
                {stats.activeOrders > 0 && (
                  <div className="absolute -top-2 -right-2 w-6 h-6 bg-yellow-500 rounded-full flex items-center justify-center">
                    <span className="text-xs font-bold text-white">{stats.activeOrders}</span>
                  </div>
                )}
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-yellow-500/10 rounded-xl flex items-center justify-center">
                    <Clock className="w-6 h-6 text-yellow-600" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  {stats.activeOrders}
                </h3>
                <p className="text-sm text-gray-600">Pesanan Aktif</p>
              </div>

              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all relative">
                {(stats.lowStockItems + stats.outOfStockItems) > 0 && (
                  <div className="absolute -top-2 -right-2">
                    <div className="relative">
                      <div className="absolute inset-0 bg-red-500 rounded-full animate-ping opacity-75"></div>
                      <div className="relative w-6 h-6 bg-red-500 rounded-full flex items-center justify-center">
                        <span className="text-xs font-bold text-white">{stats.lowStockItems + stats.outOfStockItems}</span>
                      </div>
                    </div>
                  </div>
                )}
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-red-500/10 rounded-xl flex items-center justify-center">
                    <Package className="w-6 h-6 text-red-600" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  <span className="text-red-600">{stats.outOfStockItems}</span>
                  <span className="text-gray-400 mx-1">/</span>
                  <span className="text-orange-600">{stats.lowStockItems}</span>
                </h3>
                <p className="text-sm text-gray-600">Habis / Stok Rendah</p>
              </div>
            </div>

            {/* Alerts Section */}
            {alerts.length > 0 && (
              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm mb-8">
                <div className="flex items-center gap-2 mb-4">
                  <Bell className="w-5 h-5 text-[var(--color-accent)]" />
                  <h3 className="text-lg font-bold text-gray-900">Notifikasi</h3>
                </div>
                <div className="space-y-3">
                  {alerts.map((alert) => (
                    <div
                      key={alert.id}
                      className={`p-4 rounded-lg border-l-4 ${
                        alert.type === 'error' ? 'bg-red-50 border-red-500' :
                        alert.type === 'warning' ? 'bg-yellow-50 border-yellow-500' :
                        'bg-blue-50 border-blue-500'
                      }`}
                    >
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                          <AlertTriangle className={`w-5 h-5 ${
                            alert.type === 'error' ? 'text-red-600' :
                            alert.type === 'warning' ? 'text-yellow-600' :
                            'text-blue-600'
                          }`} />
                          <p className="font-medium text-gray-900">{alert.message}</p>
                        </div>
                        <button
                          onClick={() => router.push(alert.link)}
                          className="px-4 py-2 bg-white border border-gray-300 rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors"
                        >
                          {alert.action}
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* To-Do List */}
            {todos.length > 0 && (
              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm mb-8">
                <div className="flex items-center gap-2 mb-4">
                  <CheckCircle className="w-5 h-5 text-[var(--color-accent)]" />
                  <h3 className="text-lg font-bold text-gray-900">Yang Harus Dilakukan</h3>
                </div>
                <div className="space-y-2">
                  {todos.map((todo) => (
                    <button
                      key={todo.id}
                      onClick={() => router.push(todo.link)}
                      className="w-full flex items-center justify-between p-4 hover:bg-gray-50 rounded-lg transition-colors group"
                    >
                      <div className="flex items-center gap-3">
                        <div className={`w-2 h-2 rounded-full ${
                          todo.priority === 'high' ? 'bg-red-500' :
                          todo.priority === 'medium' ? 'bg-yellow-500' :
                          'bg-blue-500'
                        }`} />
                        <span className="text-gray-900 font-medium">{todo.text}</span>
                      </div>
                      <ChevronRight className="w-5 h-5 text-gray-400 group-hover:text-[var(--color-accent)] transition-colors" />
                    </button>
                  ))}
                </div>
              </div>
            )}

            {/* Recent Transactions */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
              {/* Today's Summary */}
              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
                <h3 className="text-lg font-bold text-gray-900 mb-4">Ringkasan Hari Ini</h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between p-3 bg-green-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <DollarSign className="w-5 h-5 text-green-600" />
                      <span className="text-sm font-medium text-gray-700">Total Pendapatan</span>
                    </div>
                    <span className="text-lg font-bold text-green-600">{formatCurrency(stats.todayRevenue)}</span>
                  </div>
                  <div className="flex items-center justify-between p-3 bg-blue-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <ShoppingCart className="w-5 h-5 text-blue-600" />
                      <span className="text-sm font-medium text-gray-700">Total Pesanan</span>
                    </div>
                    <span className="text-lg font-bold text-blue-600">{stats.todayOrders}</span>
                  </div>
                  <div className="flex items-center justify-between p-3 bg-purple-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <Clock className="w-5 h-5 text-purple-600" />
                      <span className="text-sm font-medium text-gray-700">Pesanan Aktif</span>
                    </div>
                    <span className="text-lg font-bold text-purple-600">{stats.activeOrders}</span>
                  </div>
                </div>
              </div>

              {/* Quick Stats */}
              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
                <h3 className="text-lg font-bold text-gray-900 mb-4">Statistik Cepat</h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <span className="text-sm text-gray-600">Pendapatan Minggu Ini</span>
                    <span className="text-lg font-bold text-gray-900">{formatCurrency(stats.weekRevenue)}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-sm text-gray-600">Pendapatan Bulan Ini</span>
                    <span className="text-lg font-bold text-gray-900">{formatCurrency(stats.monthRevenue)}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-sm text-gray-600">Pendapatan Tahun Ini</span>
                    <span className="text-lg font-bold text-gray-900">{formatCurrency(stats.yearRevenue)}</span>
                  </div>
                  <div className="pt-3 border-t border-gray-200">
                    <button
                      onClick={() => router.push('/dashboard/analytics')}
                      className="w-full px-4 py-2 bg-[var(--color-accent)] text-white rounded-lg font-medium hover:bg-[var(--color-accent-hover)] transition-colors"
                    >
                      Lihat Analitik Lengkap
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </>
          )}
        </main>
      </>
  );
}